

































































































.block-buttons__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  > button {
    max-width: 150px;
    width: 100%;
    margin: 0 15px;
  }
}

.block-buttons__process {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  > button {
    width: 200px;
    margin-bottom: 25px;
  }
}
