





























































.search-block {
  display: flex;
  align-items: center;
  justify-content: space-around;
  max-width: 800px;
  margin: 0 auto 30px;
  background: #fbfbfb;
  padding: 20px;
  border: 1px solid #e7e7e7;
  > h1 {
    text-align: center;
    color: rgba(0, 0, 0, .6);
    font-size: 1.2rem;
  }
  > .at-input {
    flex-grow: 1;
    margin: 0 40px;
  }
}

@media screen and (max-width: 600px) {
  .search-block {
    flex-direction: column;
    > h1 {
      margin-bottom: 15px;
    }
    > .at-input {
      width: 100%;
      max-width: 400px;
    }
  }
}
