












































#loading {
  position: absolute;
  top: 0;
  right: -52px;
  bottom: 0;
  border-right: 1px solid #e7e7e7;
  background: #fff;
}

.loading__inner {
  position: sticky;
  top: 0;
}

.loading__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  line-height: 0;
  font-size: 1.7rem;
  color: #444;
  border-bottom: 1px solid #e7e7e7;
  overflow: hidden;
  transition: .2s;
  &:hover {
    background: rgba(0, 0, 0, .05);
  }
  &.loading__cancel {
    pointer-events: none;
    opacity: .5;
  }
}

.loading__icon {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1rem;
}

@media only screen and (max-width: 1300px) {
  #loading {
    display: none;
  }
}
