













































































































button {
  margin-top: 25px;
}

#token {
  margin: 0 auto;
}

.header {
  font-weight: bold;
  text-align: center;
  margin-bottom: 30px;
}

.generate {
  margin-bottom: 30px;
  text-align: center;
  > p {
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: 20px;
  }
}

.flex {
  margin-top: 25px;
  .at-input {
    margin-right: 10px;
  }
  > button {
    margin: 0;
  }
}

.info {
  margin-top: 25px;
  > .i1 {
    font-weight: bold;
    margin-bottom: 10px;
  }
  > p {
    word-wrap: break-word;
  }
}

.exists {
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
}

.logged-in {
  > .btn-out {
    text-align: center;
    > button {
      margin-top: 0;
    }
  }
}

@media screen and (max-width: 550px) {
  .flex {
    flex-direction: column;
    > .at-popover {
      margin-bottom: 20px;
    }
  }
}
