
























































































.indicators {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  .indicator {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid #e7e7e7;
    margin: 5px;
    padding: 10px 6px;
    p {
      font-weight: bold;
      margin: 0;
    }
    i {
      font-size: 1.1rem;
      margin-right: 10px;
    }
    .flex {
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
    }
    .at-input {
      margin-bottom: 20px;
    }
  }
}

@media screen and (max-width: 550px) {
  .indicators {
    justify-content: center;
  }
}
